import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        user: {},
        correctCount: 0,
        errorCount: 0,
    },
    mutations: {
        addCorrectCount(state){
            state.correctCount++
        },

        addErrorCount(state){
            state.errorCount++
        },

        setUser(state,user){
            state.user = user
        }
    }
})
