<template>
    <full-screen class="container">
        <img src="./image/index-header.png" style="width: 30%">
        <div style="font-size: 1.3rem;margin: .5rem">单词拼写能力评估</div>
        <div class="form">

            <van-radio-group v-model="form.at_school" style="display: flex;justify-content: space-between">
                <van-radio checked-color="pink" name="yes">在校生</van-radio>
                <van-radio checked-color="pink" name="no">非在校生</van-radio>
            </van-radio-group>
            <br>
            <input v-model="form.student_id" placeholder="宝贝学号" v-if="this.form.at_school === 'yes'"/>
            <input v-model="form.baby_name" placeholder="宝贝姓名"/>
            <input v-model="form.school" placeholder="所在校区" v-if="this.form.at_school === 'yes'"/>
            <input v-model="form.class" placeholder="所在班级" v-if="this.form.at_school === 'yes'"/>
            <input v-model="form.baby_age" v-if="this.form.at_school === 'no'" placeholder="宝贝年龄" type="number"/>
            <div @click="visualAddress = true">
                <input disabled :value="address" placeholder="所在地区"   v-if="this.form.at_school === 'no'"/>
            </div>
            <input v-model="form.phone" v-if="this.form.at_school === 'no'" placeholder="手机号" type="number"/>
            <van-button class="login" @click="login">登录</van-button>
        </div>

        <van-popup v-model="visualAddress" position="bottom">
            <van-area :area-list="areaList" @confirm="confirmAddress" @cancel="visualAddress = false"/>
        </van-popup>
    </full-screen>
</template>

<script>
    import FullScreen from "../../components/FullScreen";
    import area from './js/area'
    export default {
        name: "Index",
        components: {FullScreen},
        data() {
            return {
                visualAddress: false,
                form: {
                    student_id: '',
                    at_school: 'yes',
                    baby_name: '',
                    school: '',
                    class: '',
                    phone: '',
                    baby_age: '',
                    province: '',
                    city: '',
                    county: ''
                },
                areaList: area,
                users: []
            }
        },
        computed:{
          address(){
              let form = this.form
              if (form.province && form.city && form.county)
                  return form.province + '-' + form.city + '-' + form.county
              else
                  return  ''
          }
        },
        mounted(){
            if (this.$store.state.user.is_save_info === 'yes') {
                this.$router.push('/h5/comment')
            }

            this.$http.get('/user/students').then(
                res => {
                    this.users = res
                }
            )
        },
        watch:{
            'form.student_id' (val) {
                window.console.log(val)
                this.users.forEach(
                    item => {
                        if (val.toLowerCase() === item.student_id.toLowerCase()) {
                            this.form.class = item.class
                            this.form.school = item.school
                        }
                    }
                )
            }
        },
        methods: {
            confirmAddress(e){
                this.form.province = e[0].name
                this.form.city = e[1].name
                this.form.county = e[2].name
                this.visualAddress = false
            },

            login () {
                if (this.form.baby_name.length < 1){
                    this.$toast('宝宝名字不能为空')
                    return
                }
                if(this.form.at_school === 'no'){
                    if (this.form.phone.length < 1){
                        this.$toast('手机不能为空')
                        return
                    }
                    if(!(/^1[3456789]\d{9}$/.test(this.form.phone))){
                        alert("手机号码有误，请重填");
                        return false;
                    }
                }
                this.$http.post('/user/users/submit_info',this.form).then(
                    () => {
                        this.$router.push('/h5/comment')
                    }
                )
            }
        }
    }
</script>

<style scoped>
    .container{
        text-align: center;
        background-color: rgb(243,152,0);
        color: white;
        padding: 10px;
        overflow-y: scroll;
    }

    .form{
        margin: 0 30px ;
        padding: 30px;
        border: 8px #2a3282 solid;
        border-radius: 10px;
        background-color: white;
    }

    .form input{
        color: black;
        border: 0;
        background-color: #eee;
        display: block;
        margin: 10px 0;
        width: 100%;
        height: 38px;
        padding: 0 10px;
        box-sizing: border-box;
        border-radius: 10px;
    }

    .login{
        margin-top: 1rem;
        background-color: #ef496f;
        width: 10rem;
        border-radius: 3rem;
        color: white;
        border: 0;
    }
</style>
