<template>
    <div>
        <crud-index :auth="['delete']" :base-url="baseUrl"  ref="crud" :insert-form="insertForm" :edit-form="editForm"
                     :filter="filter" :table="table" :fields="fields">

            <template  slot="more">
                <el-form label-position="top">
                    <el-form-item label="批量上传">
                        <div style="border: 1px solid #e6e6e6;padding: 10px" v-if="words.length > 0">
                            <el-tag
                                    style="margin: 5px"
                                    :key="index"
                                    v-for="(tag,index) in words"
                                    closable
                                    :disable-transitions="false"
                                    @close="delWord(tag,index)">
                                {{tag.text}}
                            </el-tag>
                        </div>

                        <div id="upload" @drop="drop" >
                            <div>
                                <i class="el-icon-upload" style="font-size: 67px"></i>
                                <br>
                                <span>将文件拖拽到此处</span>
                            </div>
                        </div>
                    </el-form-item>
                </el-form>

            </template>
        </crud-index>



    </div>
</template>

<script>
    import CrudIndex from "../../components/admin/CrudIndex";

    export default {
        name: "ChineseLibrary",
        components: { CrudIndex},
        data() {
            return {
                baseUrl: '/admin/chinese',
                filter: {
                    disabledFields: ['is_translation', 'status', 'comment'],
                    hideFields: ['create_time', 'ts']
                },
                table: {
                    defaultExpandAll: true,
                    treeProps: {children: 'children'},
                    actionWidth: 400,
                    rowKey: 'id'
                },
                insertForm: {
                    disabledFields: ['is_translation', 'status', 'comment'],
                    hideFields: ['create_time', 'ts']
                },
                editForm: {
                    disabledFields: ['is_translation', 'status', 'comment'],
                    hideFields: ['create_time']
                },
                fields: [
                    {name: 'text', comment: '文字' ,width: 50},
                    {name: 'similar', comment: '形近字',with: 50},
                    {name: 'voice', comment: '声音'},
                ],
                parentOptions: [],
                words: [],
                word: '',
                visualSimilarManager: false,
                selectCategory: {},

            }
        },


        created() {
            //解禁当前元素为可放置被拖拽元素的区域
            document.ondragover = function (ev) {
                // var ev=ev||window.event;
                ev.preventDefault();
            }
            document.ondrop = function (e) {
                //解禁当前元素为可放置被拖拽元素的区域
                e.preventDefault();
            }
        },

        mounted () {
            this.initFields()
        },

        methods: {
            async initFields(){
                let similarOptions = await this.$http.get('/admin/chinese/all')
                similarOptions.push({
                    id: 0,
                    text: '无'
                })
                this.fields = [
                    {name: 'text', comment: '文字', validate: [{required: true, message: '文字不能为空'}],width: 100},
                    {name: 'similar_id', comment: '形近字',default: 0, type: 'select',options:similarOptions,width: 100,valueName: 'id',labelName: 'text',filterable: true},
                    {name: 'voice', comment: '声音', validate: [{required: true, message: '声音不能为空'}],type: 'file',mode: 'text'},
                ]
            },

            refresh(){
                this.$refs.crud.refresh()
            },

            submit(){
                return this.$http.post(this.baseUrl,this.form)
            },

            addChildren(row){
                this.form.pid = row.id
                this.visualAddChildren = true
            },

            similarManager(row){

            },

            async delWord(row,index){
                await this.$http.delete(this.baseUrl + row.id)
                this.words.splice(index,1)
            },


           async drop(e) {
                let files = [].slice.call(e.dataTransfer.files)
                if (!files.length) return
                if (files.length > 20) {
                    this.$notify.error('每次最多上传20个文件')
                    return;
                }
                files = files.filter(file => file.name.split('.')[0].length === 1 && file.name.split('.')[1] === 'mp3')
                let form = new FormData()
               files.forEach(file => {
                    form.append('files[]', file, file.name)
                })
                form.append('category',this.category)
                let loading = this.$loading({
                    mask: true,
                    text: '正在上传请稍等...'
                })
                await this.$http.post(`/admin/chinese/batch`, form)
                loading.close()

                this.$notify.success('上传成功')
                this.wordManager(this.selectCategory)
            },


        }
    }
</script>

<style scoped>
    #upload {
        text-align: center;
        height: 200px;
        margin: 10px 0;
        border: 1px dashed #409EFF;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    #upload:hover {
        border: 1px dashed #77f2ff;
    }


    .files {
        display: flex;
        justify-content: start;
        flex-flow: row wrap;
    }

    .files span {
        border: 1px dashed #cccccc;
        padding: 5px;
        margin: 5px;
    }
</style>
