<template>
    <div>
        <crud-index :auth="[]" :baseUrl="baseUrl" :insert-form="insertForm" :edit-form="editForm" :filter="filter" :table="table" :fields="fields">
            <template  #table-action-left="scope">
                <el-button plain type="primary" size="mini" @click="chineseLibraryManager(scope.row)" >成绩查看</el-button>
            </template>
        </crud-index>

        <el-dialog :modal="false" :visible.sync="visualChineseLibraryManager" title="成绩查看">
            <el-table
                    :data="tableData"
                    style="width: 100%">
                <el-table-column
                        prop="score"
                        label="分数">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        label="获得时间">
                </el-table-column>
            </el-table>
        </el-dialog>
    </div>
</template>

<script>
    import CrudIndex from "../../components/admin/CrudIndex";
    export default {
        name: "ChineseLevel",
        components: {CrudIndex},
        data() {

            return {
                tableData: [],
                baseUrl: '/admin/users',
                filter: {
                    hideFields: ['password','create_time']
                },
                table: {
                    actionWidth: 200,
                    hideFields: ['password']
                },
                insertForm: {
                    hideFields: ['create_time']
                },
                editForm: {
                    hideFields: ['create_time']
                },
                fields: [
                    // {name: 'wx_openid',comment: 'openid'},
                    {name: 'baby_name',comment: '宝宝名字'},
                    {name: 'at_school',comment: '是否在校'},
                    {name: 'province',comment: '所在省份'},
                    {name: 'city',comment: '所在城市'},
                    {name: 'country',comment: '所在县/区'},
                    {name: 'baby_age',comment: '宝贝年龄'},
                    {name: 'school',comment: '校区'},
                    {name: 'class',comment: '班级'},
                    {name: 'phone',comment: '手机'},
                ],
                selectChineseLibrary: {},
                selectChinese: {},
                words: [],

                selectChineseId: '',
                visualChineseLibraryManager: false
            }
        },

        mounted(){
            // this.initFields()
            // this.$http.get('/admin/chinese/all').then(
            //     res => {
            //         this.allChineseOptions = res
            //     }
            // )
        },
        methods: {


            async delWord(row,index){
                await this.$http.delete(this.baseUrl + '/' + this.selectChineseLibrary.id + '?relation=chinese&relation_id=' + row.id)
                this.words.splice(index,1)
            },

            async insertWord(selectChineseId){

                let res = await this.$http.post(this.baseUrl + '/' + this.selectChineseLibrary.id + '/chinese/' +  selectChineseId )
                this.words.push(res)

            },

            async chineseLibraryManager(row){
                this.selectChineseLibrary = row
                this.tableData = await this.$http.get(`/admin/users/${this.selectChineseLibrary.id}/chinese_result`)
                this.visualChineseLibraryManager = true
            }
        }
    }
</script>

