<template>
    <div>
        <van-button @click="test1" size="large">测试1</van-button>
        <van-button @click="test2" size="large">测试2</van-button>
<!--        <van-button @click="test3" size="large">测试3</van-button>-->
<!--        <van-button @click="test4" size="large">测试4</van-button>-->
        <van-button @click="test5" size="large">测试5</van-button>
    </div>
</template>

<script>
    export default {
        name: "Test1",
        data() {
            return {}
        },
        methods: {
            test1(){
                let audio = new Audio('http://sz.grandcomms.cn/uploads/1ncVLHvKaikRRmlKGIPipvHULb26NzFPT5s6v71W.mp3')
                let  p = audio.play()
                if (!(p instanceof Promise)){
                    alert('no promise')
                    return
                } else {
                    p.catch(err => {alert(err)})
                }
                audio.onended = () => {
                    audio = new Audio()
                    audio.src  = 'audio/letter/a.mp3'
                    audio.play()
                    audio.onended = () => this.$toast('执行完毕')
                }
            },

            test2(){
                new Promise(resolve => {
                    let audio = new Audio('http://sz.grandcomms.cn/uploads/1ncVLHvKaikRRmlKGIPipvHULb26NzFPT5s6v71W.mp3')
                    let  p = audio.play()
                    if (!(p instanceof Promise)){
                        alert('no promise')
                        return
                    } else {
                        p.catch(err => {alert(err)})
                    }
                    audio.onended = () => {
                        audio = new Audio()
                        audio.src  = 'audio/letter/a.mp3'
                        audio.play()
                        resolve()
                        audio.onended = () => this.$toast('执行完毕')
                    }
                })
            },

            test5(){
                let audio = new Audio()
                let url = 'audio/letter/a.mp3'
                audio.src = url
                audio.play()
                setTimeout(() => {
                    let audio = new Audio()
                    let url = 'http://sz.grandcomms.cn/uploads/1ncVLHvKaikRRmlKGIPipvHULb26NzFPT5s6v71W.mp3'
                    audio.src = url
                    audio.play()
                    audio.onended = () => this.$toast('执行完毕')
                },3000)
            },
        }
    }
</script>

