<template>
    <div>
        <crud-index :baseUrl="baseUrl" :insert-form="insertForm" :edit-form="editForm" :filter="filter" :table="table" :fields="fields">
            <template  #table-action-left="scope">
                <el-button plain type="primary" size="mini" @click="chineseLibraryManager(scope.row)" >字库管理</el-button>
            </template>
        </crud-index>

        <el-dialog :modal="false" :visible.sync="visualChineseLibraryManager" title="字库管理">
            <el-tag
                    style="margin: 5px"
                    :key="index"
                    v-for="(tag,index) in words"
                    closable
                    :disable-transitions="false"
                    @close="delWord(tag,index)">
                {{tag.text}}
            </el-tag>
            <el-select v-model="selectChineseId" @change="insertWord" placeholder="请选择" filterable>
                <el-option
                        v-for="item in chineseOptions"
                        :key="item.id"
                        :label="item.text"
                        :value="item.id">
                </el-option>
            </el-select>
        </el-dialog>
    </div>
</template>

<script>
    import CrudIndex from "../../components/admin/CrudIndex";
    export default {
        name: "ChineseLevel",
        components: {CrudIndex},
        data() {

            return {
                baseUrl: '/admin/chinese_level',
                filter: {
                    hideFields: ['password','create_time']
                },
                table: {
                    actionWidth: 300,
                    hideFields: ['password']
                },
                insertForm: {
                    hideFields: ['create_time']
                },
                editForm: {
                    hideFields: ['create_time']
                },
                fields: [
                    {name: 'name',comment: '关卡名称'},
                    {name: 'library_id',comment: '字库'},
                    {name: 'mode',comment: '模式'}
                ],
                selectChineseLibrary: {},
                selectChinese: {},
                words: [],
                allChineseOptions: [],
                selectChineseId: '',
                visualChineseLibraryManager: false
            }
        },
        computed: {
            chineseOptions(){
                return this.allChineseOptions.filter(option => {
                    if (this.selectChineseLibrary.mode === '2-1' && option.similar_id === 0){
                        return false
                    }  else {
                        return  true
                    }
                })
            }
        },
        mounted(){
            this.initFields()
            this.$http.get('/admin/chinese/all').then(
                res => {
                    this.allChineseOptions = res
                }
            )
        },
        methods: {
            async initFields(){
                // let chineseLibrary = await this.$http.get('/admin/chinese_libraries/all')
                this.fields = [
                    {name: 'name',comment: '关卡名称',validate: [{required: true,message: '关卡名称不能为空'}]},
                    // {name: 'library_id',comment: '字库',validate: [{required: true,message: '字库不能为空'}],options: chineseLibrary,type: 'select',labelName: 'name',valueName: 'id'},
                    {name: 'mode',comment: '模式',validate: [],default: '4-1',type: 'radio',options: [{label: '四选一',value: '4-1'},{label: '三选一',value: '3-1'},{label: '二选一',value: '2-1'},{label: '认一认',value: 'recognize'}]},
                ]
            },

            async delWord(row,index){
                await this.$http.delete(this.baseUrl + '/' + this.selectChineseLibrary.id + '?relation=chinese&relation_id=' + row.id)
                this.words.splice(index,1)
            },

            async insertWord(selectChineseId){

                let res = await this.$http.post(this.baseUrl + '/' + this.selectChineseLibrary.id + '/chinese/' +  selectChineseId )
                this.words.push(res)

            },

            async chineseLibraryManager(row){
                this.selectChineseLibrary = row
                this.words = await this.$http.get(`/admin/chinese_level/${this.selectChineseLibrary.id}/chinese`)

                this.visualChineseLibraryManager = true
            }
        }
    }
</script>

