<template>
    <full-screen>
        <div v-if="question">
            <van-progress
                    pivot-text=""
                    style="border-radius: 0"
                    color="#f2826a"
                    :percentage="percentage"
                    :stroke-width="18"
            />
            <div style="text-align: center">
                <img class="question-image" :src="question.image">
            </div>

            <div style="text-align: center;font-size: 3rem;">
                <div v-if="answerStatus === 'normal'">
                    <span v-for="(letter,index) in question.text"
                          :key="index">{{letterIndex === index?'_':letter}}</span>
                </div>
                <div v-if="answerStatus === 'correct'">
                    <span v-for="(letter,index) in question.text" :key="index"
                          :style="{color: letterIndex === index?'green':''}">{{letter}}</span>
                </div>
                <div v-if="answerStatus === 'error'">
                    <span v-for="(letter,index) in question.text" :key="index"
                          :style="{color: letterIndex === index?'red':''}">{{letter}}</span>
                </div>
            </div>

            <div style="padding: 0 3rem ;">
                <van-row>
                    <van-col
                            :span="24 / (optionNumber / 2)" v-for="(letter,index) in letterArray" :key="index">
                        <div class="letter" @click="selectLetter(letter.text,index)"
                             v-if="selectIndex !== index || answerStatus === 'normal'">
                            {{letter.text}}
                        </div>
                        <div class="letter" @click="selectLetter(letter.text,index)" v-else
                             :style="{backgroundColor: answerStatus === 'error'?'red':'green',borderColor:answerStatus === 'error'?'red':'green'}">
                            {{letter.text}}
                        </div>
                    </van-col>
                </van-row>
            </div>

            <br>
            <br>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'normal'">
                <img src="./image/test/listen_btn.png" style="width: 20%" @click="playAnswer"/>
            </div>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'correct'">
                <img src="./image/true_btn.png" style="width: 35%"/>
            </div>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'error'">
                <img src="./image/false_btn.png" style="width: 35%"/>
            </div>

            <div style="text-align: center">
                {{questionCount}}/{{questionTotal}}
            </div>
        </div>
        <div v-else style="margin-top: 3rem;text-align: center">
            <van-loading type="spinner" color="#1989fa"/>
        </div>

        <van-dialog v-model="visualLevelTip" :show-confirm-button="false">
            <img src="./image/level-1-tip.png" style="width: 100%" @click="begin"/>
        </van-dialog>

        <audio ref="audio" style="display: none"></audio>
    </full-screen>
</template>

<script>
    import FullScreen from "../../components/FullScreen";

    let beginAudio = null
    let gameTimer = null
    export default {
        name: "Level-1",
        components: {FullScreen},
        data() {
            return {
                visualLevelTip: true,
                question: null,
                text: '',
                levelName: 'level-1',
                timeCount: 0,
                timeTotal: 15,
                optionNumber: 6,
                questionCount: 0,
                questionTotal: 10,
                letterIndex: 0,
                letterAnswer: '',
                answerStatus: 'normal',
                letterArray: [],
                lock: false,
                selectIndex: -1,
                questionQueue: []
            }
        },
        beforeDestroy() {
            window.console.log('清理定时器')
            clearInterval(gameTimer);
        },
        async mounted() {
            beginAudio = new Audio('audio/level/level-1.mp3')
            beginAudio.play()
            let res = await this.$http.get('user/words/level/' + this.levelName)
            this.questionQueue = res.sort(() => Math.random() - 0.5)
            if (this.questionQueue.length < this.questionTotal) {
                this.questionTotal = this.questionTotal.length
            }
        },
        computed: {
            percentage() {
                return this.timeCount / this.timeTotal * 100
            },
        },
        methods: {
            generateLetterArray() {
                let letters = []
                let answer = this.letterAnswer
                for (let i = 97; i <= 122; i++) {
                    let char = String.fromCharCode(i)
                    if ('aeiou'.indexOf(char) < 0) {
                        letters.push({
                            text: char
                        })
                    }
                }
                letters = letters.filter(letter => letter.text !== answer)
                letters = letters.sort(() => Math.random() - 0.5).splice(0, this.optionNumber - 1)
                letters.push({text: answer})
                letters = letters.sort(() => Math.random() - 0.5)
                return letters
            },

            async answerCorrect() {
                this.answerStatus = 'correct'
                clearInterval(gameTimer)
                this.$store.commit('addCorrectCount')
                await this.playAnswer()
                this.switchWord()

            },

            async answerError() {
                this.answerStatus = 'error'
                clearInterval(gameTimer)
                this.$store.commit('addErrorCount')
                await this.playAnswer()
                this.switchWord()

            },


            selectLetter(letter, index) {
                if (this.lock) return
                this.lock = true
                this.selectIndex = index
                if (letter === this.letterAnswer) {
                    this.answerCorrect()
                } else {
                    this.answerError()
                }
            },

            begin() {

                if (beginAudio) beginAudio.pause()
                this.visualLevelTip = false

                this.questionCount = 0

                this.switchWord()
            },

            async switchWord() {
                clearInterval(gameTimer)
                try {
                    // let res = await this.$http.get('user/words/level/' + this.levelName)
                    let res = this.questionQueue[this.questionCount]
                    this.questionCount++
                    if (this.questionCount > this.questionTotal) {
                        this.switchLevel()
                        return
                    }
                    this.timing()
                    this.selectIndex = -1
                    this.answerStatus = 'normal'
                    this.question = res
                    this.letterIndex = this.question.index - 1
                    this.letterAnswer = res.text[this.letterIndex]
                    this.letterArray = this.generateLetterArray()
                    setTimeout(async () => {
                        await this.playAnswer()
                        // this.$toast('声音播放完毕')
                        this.lock = false
                    })

                } finally {
                    this.$loading = false
                }
            },

            timing() {
                clearInterval(gameTimer)
                this.timeCount = 0
                gameTimer = setInterval(() => {
                    if (this.timeCount >= this.timeTotal) {
                        clearInterval(gameTimer)
                        this.timeEnd()
                    } else {
                        this.timeCount++
                    }
                }, 1000)
            },

            timeEnd() {
                this.answerError()
            },

            switchLevel() {
                clearInterval(gameTimer)
                this.$router.replace('/h5/level-2')
            },

            playAnswer() {
                return new Promise((async (resolve) => {
                        try {
                            let url = 'audio/letter/' + this.letterAnswer + '.mp3'
                            let audio = new Audio(url)
                            await audio.play()
                            audio.onended = async () => {
                                try {
                                    let url = this.question.audio
                                    let audio = new Audio(url)
                                    await audio.play()
                                    audio.onended = () => resolve()
                                }catch (e) {
                                    resolve()
                                    this.$toast(e)
                                }
                            }
                        } catch
                            (e) {
                            resolve()
                            this.$toast(e)
                        }
                    }
                ))
            }

        }
    }
</script>

<style scoped>
    .question-image {
        width: 13rem;
        height: 13rem;
    }

    .letter {
        font-size: 1.3rem;
        margin: 10px auto;
        font-weight: bold;
        width: 2rem;
        height: 2rem;
        border: 1px solid #1989fa;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
    }
</style>
