// 由于懒加载页面太多的话会造成webpack热更新太慢，所以开发环境不使用懒加载，只有生产环境使用懒加载
const _import = process.env.NODE_ENV === 'development' ? file => require('@/pages/' + file).default : file => () => import('@/pages/' + file)

const sortRouter = (path, name = null, component = null) => {
    if (!component) {
        component = path.split('/:')[0]
    }
    if (!name) {
        name = component.replace(/\//g, "-")
    }
    return {
        name: name,
        path: '/' + path,
        component: require('@/pages/' + component).default
    }
}


export default [
    {
        name: 'admin-app',
        component: _import('AdminApp'),
        path: '/admin',
        redirect: '/admin/welcome',
        children: [
            {
                name: 'admin-container',
                component: _import('admin/Container'),
                path: '/admin/container',
                redirect: '/admin/welcome',
                children: [
                    sortRouter('admin/welcome', 'welcome', 'admin/Welcome'),
                    sortRouter('admin/crud', 'crud', 'admin/Crud'),
                    sortRouter('admin/admin', 'crud', 'admin/Admin'),
                    sortRouter('admin/chinese_library', 'chinese_library', 'admin/ChineseLibrary'),
                    sortRouter('admin/chinese_level', 'chinese_level', 'admin/ChineseLevel'),
                    sortRouter('admin/user', 'user', 'admin/User'),
                    sortRouter('admin/word', 'word', 'admin/Word'),
                ]
            },
            sortRouter('admin/login', 'crud', 'admin/Login'),
        ]
    },
    {
        name: 'h5-app',
        component: _import('H5App'),
        path: '/h5',
        redirect: '/h5/index',
        children: [
            sortRouter('h5/index', 'index', 'h5/Index'),
            sortRouter('h5/comment', 'comment', 'h5/Comment'),
            sortRouter('h5/test', 'test', 'h5/Test'),
            sortRouter('h5/test1', 'test1', 'h5/Test1'),
            sortRouter('h5/level-1', 'level-1', 'h5/Level-1'),
            sortRouter('h5/level-2', 'level-2', 'h5/Level-2'),
            sortRouter('h5/level-3', 'level-3', 'h5/Level-3'),
            sortRouter('h5/result', 'result', 'h5/Result'),
        ]
    },
    sortRouter('*', '404', '404')
]
