    </div>
</template>

<script>
    import Vue from 'vue'
    import Vant from 'vant';
    import 'vant/lib/index.css';
    import { Notify } from 'vant';
    Vue.use(Vant);

    export default {
        name: "H5App",
        data() {
            return {
                visualContent: false,
                visualLoginConfirm: false
            }
        },
        created(){
            window.onload = function() {
                document.addEventListener('touchstart', function(event) {
                    if (event.touches.length > 1) {
                        event.preventDefault()
                    }
                })
                document.addEventListener('gesturestart', function(event) {
                    event.preventDefault()
                })
            }
            // 重写401错误处理方法
            this.$http.err401 = () => {
                this.visualLoginConfirm = true
                this.jumpLogin()
            }


            // 重写错误消息处理方法
            this.$http.errMsg = msg => {
                this.$toast(msg)
            }


            Vue.prototype.$loading = Notify


            this.disableScala()
        },
        async mounted(){
            let res = await this.$http.get('/user/users/is_login') // 请求出错自动跳转
            this.$store.commit('setUser',res)
            this.visualContent = true
        },
        methods: {
            disableScala(){
                window.onload = function () {
                    document.addEventListener('touchstart', function (event) {
                        if (event.touches.length > 1) {
                            event.preventDefault();
                        }
                    });
                    var lastTouchEnd = 0;
                    document.addEventListener('touchend', function (event) {
                        var now = (new Date()).getTime();
                        if (now - lastTouchEnd <= 300) {
                            event.preventDefault();
                        }
                        lastTouchEnd = now;
                    }, false);
                    document.addEventListener('gesturestart', function (event) {
                        event.preventDefault();
                    });
                }
            },

            clickMe(){
                try {
                    // let audio = new Audio('/uploads/pORZgdzJD6uXPnfCWA6YQhGlPjniKsNKsB4BAlS8.mp3')
                    // audio.play()
                    // this.$refs.audio.load()
                    // alert(this.$refs.audio.src)
                    // alert(this.$refs.audio.readyState)
                    // this.$refs.audio.src
                    // this.$refs.audio.play()
                }catch (e) {
                    alert(e)
                }
            },

            jumpLogin(){
                // this.visualLoginConfirm = false
                // this.$router.push('/admin/login')
                // window.console.log(21321)
                window.location.href = '/user/wechat/scope'
            }
        }
    }
</script>

