<template>
    <full-screen>
        <div v-if="question">
            <van-progress
                    pivot-text=""
                    style="border-radius: 0"
                    color="#f2826a"
                    :percentage="percentage"
                    :stroke-width="18"
            />
            <div style="text-align: center">
                <img class="question-image" :src="question.image">
            </div>

            <div  class="horizontal-center" :class="{blink:blink}">
                <template v-for="(letter,index) in question.text" >
                    <div v-if="index !== letterIndex" class="letter-normal" :key="index" >
                        {{letter}}
                    </div>
                    <div v-else-if="answerStatus === 'normal'" :key="index" class="letter-index"></div>
                    <div v-else-if="answerStatus === 'error'" :key="index" class="letter-error">{{letter}}</div>
                    <div v-else-if="answerStatus === 'correct'" :key="index" class="letter-correct">{{letter}}</div>
                </template>
            </div>

            <div style="padding: 0 3rem ;margin: 2rem 0" class="horizontal-center">
                <template v-for="(letter,index) in letterArray" >
                    <div class="letter" @click="selectLetter(letter.text,index)"  v-if="selectIndex !== index || answerStatus === 'normal'" :key="index">
                        {{letter.text}}
                    </div>
                    <div class="letter" @click="selectLetter(letter.text,index)"
                         :key="index"
                         v-else :style="{backgroundColor: answerStatus === 'error'?'red':'green',borderColor:answerStatus === 'error'?'red':'green'}">
                        {{letter.text}}
                    </div>
                </template>
            </div>

            <br>
            <br>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'normal'">
                <img src="./image/test/listen_btn.png" style="width: 20%" @click="playAnswer"/>
            </div>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'correct'">
                <img src="./image/true_btn.png" style="width: 35%"/>
            </div>
            <div style="text-align: center;margin-top: 1rem" v-if="answerStatus === 'error'">
                <img src="./image/false_btn.png" style="width: 35%"/>
            </div>

            <div style="text-align: center">
                {{questionCount}}/{{questionTotal}}
            </div>
        </div>
        <div v-else style="margin-top: 3rem;text-align: center">
            <van-loading type="spinner" color="#1989fa"/>
        </div>

        <van-dialog v-model="visualLevelTip" :show-confirm-button="false">
            <img src="./image/level-2-tip.png" style="width: 100%" @click="begin"/>
        </van-dialog>
    </full-screen>
</template>

<script>
    import FullScreen from "../../components/FullScreen";
    let beginAudio
    let gameTimer = null
    export default {
        name: "Level-3",
        components: {FullScreen},
        data() {
            return {
                blink: false,
                visualLevelTip: true,
                question: null,
                text: '',
                levelName: 'level-3',
                timeCount: 0,
                timeTotal: 25,
                optionNumber: 5,
                questionCount: 1,
                questionTotal: 15,
                letterIndex: 0,
                letterAnswer: '',
                answerStatus: 'normal',
                correctCount: 0,
                errorCount: 0,
                letterArray: [],
                lock: false,
                selectIndex: -1,
                questionQueue: []
            }
        },
        async mounted() {
            // this.begin()
            beginAudio = new Audio('audio/level/level-2.mp3')
            beginAudio.play()
            let res = await this.$http.get('user/words/level/' + this.levelName)
            this.questionQueue = res.sort(() => Math.random() - 0.5)
            if (this.questionQueue.length < this.questionTotal){
                this.questionTotal = this.questionTotal.length
            }
        },
        computed: {
            percentage() {
                return this.timeCount / this.timeTotal * 100
            },

        },
        beforeDestroy() {
            clearInterval(gameTimer);
        },
        methods: {
            generateLetterArray() {
                let letters = []
                // let answer = this.letterAnswer
                for (let i = 97; i <= 122; i++) {
                    let char = String.fromCharCode(i)
                    if ('aeiou'.indexOf(char) >= 0) {
                        letters.push({
                            text: char
                        })
                    }
                }
                return letters
            },

            async answerCorrect() {
                this.answerStatus = 'correct'
                this.blink = true
                clearInterval(gameTimer)
                this.$store.commit('addCorrectCount')
                await this.playAnswer()
                this.switchWord()
                // setTimeout(() => {
                //
                // }, 1500)
            },

            async answerError() {
                this.answerStatus = 'error'
                clearInterval(gameTimer)
                this.$store.commit('addErrorCount')
                await this.playAnswer()
                this.switchWord()
                // setTimeout(() => {
                //
                // }, 1500)
            },


            selectLetter(letter,index){
                if (this.lock) return
                this.selectIndex = index
                this.lock = true
                if (letter === this.letterAnswer){
                    this.answerCorrect()
                }  else {
                    this.answerError()
                }
            },

            begin(){
                if (beginAudio) beginAudio.pause()
                this.visualLevelTip = false

                this.questionCount = 0

                this.switchWord()
            },

            timing(){
                clearInterval(gameTimer)
                this.timeCount = 0
                gameTimer = setInterval(() => {
                    if (this.timeCount >= this.timeTotal){
                        clearInterval(gameTimer)
                        this.timeEnd()
                    } else  {
                        this.timeCount++
                    }
                },1000)
            },

            timeEnd(){
                this.answerError()
            },

            async switchWord() {
                clearInterval(gameTimer)
                try {
                    // let res = await this.$http.get('user/words/level/' + this.levelName)
                    let res = this.questionQueue[this.questionCount]
                    this.blink = false
                    this.questionCount++
                    if (this.questionCount > this.questionTotal){
                        this.switchLevel()
                        return
                    }
                    this.timing()
                    this.selectIndex = -1
                    this.answerStatus = 'normal'
                    this.question = res
                    // this.letterIndex = parseInt(Math.round(Math.random() * res.text.length - 1))
                    this.letterIndex = this.question.index - 1
                    this.letterAnswer = res.text[this.letterIndex]
                    window.console.log(this.letterIndex,this.letterAnswer)
                    this.letterArray = this.generateLetterArray()
                    setTimeout(async () => {
                        await this.playAnswer()
                        this.lock = false
                    } )
                } finally {
                    this.blink = false
                    this.$loading = false
                }
            },

            switchLevel() {
                clearInterval(gameTimer)
                this.$router.replace('/h5/result')
            },

            playAnswer() {
                return new Promise((async (resolve) => {
                        try {
                            let url = 'audio/letter/' + this.letterAnswer + '.mp3'
                            let audio = new Audio(url)
                            await audio.play()
                            audio.onended = async () => {
                                try {
                                    let url = this.question.audio
                                    let audio = new Audio(url)
                                    await audio.play()
                                    audio.onended = () => resolve()
                                }catch (e) {
                                    resolve()
                                    this.$toast(e)
                                }
                            }
                        } catch
                            (e) {
                            resolve()
                            this.$toast(e)
                        }
                    }
                ))
            }
        }
    }
</script>

<style scoped>
    .letter-normal,.letter-correct,.letter-error,.letter-index{
        width: 2rem;
        height: 2rem;
        border-radius: 0.5rem;
        margin: 3px;
        text-align: center;
        font-size: 1.3rem;
        line-height: 2rem;
        color: white;
        display: inline-block;
    }

    .letter-normal{
        background-color: #0face6;
    }

    .letter-error{
        background-color: red;
    }

    .letter-correct{
        background-color: green;
    }

    .letter-index{
        background-color: #e6e6e6;
    }


    .question-image {
        width: 13rem;
        height: 13rem;
    }

    .letter {
        font-size: 1.3rem;
        margin: 10px auto;
        font-weight: bold;
        width: 2rem;
        height: 2rem;
        border: 1px solid #1989fa;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
    }

    .blink{
        animation: blink 1s infinite;
    }

    @keyframes blink {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 100;
        }
        100% {
            opacity: 0;
        }
    }
</style>
