<template>
    <full-screen class="container">
        <img src="./image/test/left-bg.png" style="width: 35%;position: absolute;left: 0;top: 0"/>

        <img v-if="level.mode !== 'recognize'" src="./image/test/listenword_title.png" style="width: 30%"/>
        <img v-else src="./image/test/cen2-bg.png" style="width: 30%"/>

        <div class="time" @click="selectAnswer()">
            <img src="./image/test/time.png" height="30" width="30" style="position: relative;left: -10px"/>
            <label style="line-height: 20px;position: relative;right: 5px">
                00:{{ (Array(2).join(0) + countDown).slice(-2)}}
            </label>
        </div>

        <div class="question">
            <van-row :gutter="20" v-if="level.mode !== 'recognize'">
                <van-col :span="12" v-for="(item,index) in chinese" :key="index">
                    <!--            <div v-for="(item,index) in chinese" :key="index">-->
                    <div class="word" @click="selectAnswer(index)" v-if="isShowWord(index)">
                        {{item.text}}
                        <img class="status" src="./image/test/correct.png" v-show="item.status === 'correct'">
                        <img class="status" src="./image/test/error.png" v-show="item.status === 'error'">
                    </div>
                    <img src="./image/test/w_b.png" style="width: 100%;height: 160px;margin: 10px 0;" v-else/>
                    <!--            </div>-->
                </van-col>
            </van-row>

            <div v-else>
                <div class="word" style="height: 300px">
                    {{answer.text}}
                </div>
            </div>
        </div>


        <div style="text-align: center" v-if="level.mode !== 'recognize'">
            <img src="./image/test/listen_btn.png" style="width: 20%" @click="playAnswer"/>
        </div>

        <div v-else style="text-align: center;margin-top: 20px">
            <img src="./image/test/true_btn.png" style="width: 20%;margin-right: 60px" @click="answerCorrect"/>
            <img src="./image/test/false_btn.png" style="width: 20%" @click="answerError"/>
        </div>

        <div style="margin-top: 10px">
            <label v-if="levelIndex <= 30">
                {{levelIndex }}/30
            </label>
            <label v-if="levelIndex > 30 && levelIndex <= 40">
                {{levelIndex - 30 }}/10
            </label>
            <label v-if="levelIndex > 40 && levelIndex <= 45">
                {{levelIndex - 40}}/5
            </label>
            <label v-if="levelIndex > 45 && levelIndex <= 50">
                {{levelIndex - 45}}/5
            </label>
        </div>


        <van-dialog v-model="showLevel1Tip" :show-confirm-button="false">
            <img src="./image/test/level-1.png" style="width: 100%" @click="beginGame"/>
        </van-dialog>

        <van-dialog v-model="showLevel2Tip" :show-confirm-button="false">
            <img src="./image/test/level-2.png" style="width: 100%" @click="beginGame"/>
        </van-dialog>
    </full-screen>
</template>

<script>
    import FullScreen from "../../components/FullScreen";
    import Level1Audio from './audio/test/level1.mp3'
    import Level2Audio from './audio/test/level2.mp3'
    import goodAudio from './audio/test/good.mp3'
    import comeonAudio from './audio/test/comeon.mp3'

    let countDownTimer = null

    export default {
        name: "Test",
        components: {FullScreen},
        data() {
            return {
                showLevel1Tip: true,
                showLevel2Tip: false,
                showLevel2Flag: true,
                countDown: 15,
                level: {},
                chinese: [],
                answer: {},
                levelIndex: 0,
                errorCount: 0,
                correctCount: 0,
                levelUrls: [],
                level1Count: 1,
                level1Number: 30,
                level2Count: 1,
                level2Number: 10,
                level3Count: 1,
                level3Number: 5,
                level4Count: 1,
                level4Number: 5,
            }
        },

        mounted() {
            this.levelUrls = []
            // for (let i=0;i < this.level1Number ;i++){
            //     this.levelUrls.push( '/user/chinese_level/1')
            // }
            for (let i = 0; i < this.level2Number; i++) {
                this.levelUrls.push('/user/chinese_level/3')
            }
            for (let i = 0; i < this.level3Number; i++) {
                this.levelUrls.push('/user/chinese_level/4')
            }
            for (let i = 0; i < this.level4Number; i++) {
                this.levelUrls.push('/user/chinese_level/5')
            }
            this.countDown = 15
            this.levelIndex = 0
            this.errorCount = 0
            this.showLevel2Flag = true
            this.correctCount = 0
            // this.showLevel1()
        },
        methods: {
            showLevel1() {
                this.showLevel1Tip = true
                let audio = new Audio(Level1Audio)
                audio.play()
            },

            showLevel2() {
                this.countDown = 25
                this.showLevel2Flag = false
                this.showLevel2Tip = true
                let audio = new Audio(Level2Audio)
                audio.play()
            },

            answerCorrect() {
                clearInterval(gameTimer)
                this.correctCount++
                if (countDownTimer) clearInterval(countDownTimer)
                setTimeout(() => {
                    this.loadLevel()
                }, 1500)
            },

            answerError() {
                clearInterval(countDownTimer)
                let audio = new Audio(comeonAudio)
                audio.play()
                this.errorCount++
                if (countDownTimer) clearInterval(countDownTimer)
                setTimeout(() => {
                    this.loadLevel()
                }, 1500)
            },

            isShowWord(index) {
                if (this.level.mode === '3-1' && index > 2) return false
                if (this.level.mode === '2-1' && index > 1) return false
                return true
            },

            beginGame() {
                this.showLevel1Tip = false
                this.showLevel2Tip = false
                this.loadLevel()
            },

            playAnswer() {
                if (this.levelIndex > 30) {
                    let audio = new Audio(this.answer.voice)
                    audio.play()
                }
            },

            selectAnswer(index) {
                // this.$toast('3123')
                window.console.log(3123)
                let answer = this.chinese[index]
                if (this.answer.id !== answer.id) {
                    answer.status = 'error'
                    this.errorCount++
                } else {
                    answer.status = 'correct'
                    this.correctCount++
                }
                this.$forceUpdate()
                clearInterval(countDownTimer)
                this.loadLevel()

            },

            loadLevel() {
                if (this.levelIndex >= this.levelUrls.length) {
                    this.$router.push('/h5/result/' + this.correctCount)
                    return
                }

                if (this.levelIndex == 30 && this.showLevel2Flag) {
                    this.showLevel2()
                    return;
                }
                const url = this.levelUrls[this.levelIndex]
                this.levelIndex++
                this.$http.get(url).then(
                    res => {
                        this.level = res.level
                        this.chinese = res.chinese
                        let index = 0
                        switch (this.level.mode) {
                            case '2-1':
                                this.chinese[1] = this.chinese[0].similar
                                index = 1
                                break
                            case '3-1':
                                index = 2
                                break
                            case '4-1':
                                index = 3
                                break
                        }
                        index = parseInt(Math.round(Math.random() * index))
                        this.answer = this.chinese[index]

                        this.chinese = this.chinese.map(item => {
                            item.status = 'normal'
                            return item
                        })
                        this.playAnswer()
                        clearInterval(countDownTimer)
                        this.countDown = 15
                        countDownTimer = setInterval(() => {
                            this.countDown--
                            if (this.countDown <= 0) {
                                clearInterval(countDownTimer)
                                this.loadLevel()
                            }
                        }, 1000)

                    }
                )
            },
        }
    }
</script>

<style scoped>
    .container {
        overflow-y: scroll;
        text-align: center;
        background-color: #8fc31f;
        color: white;
        padding: 20px;
    }

    .time {
        position: absolute;
        top: 30px;
        right: 20px;
        display: flex;
        width: 23%;
        align-items: center;
        background-color: #006002;
        border-radius: 30px;
        height: 30px;
    }

    .form input {

        color: black;
        border: 0;
        background-color: #eee;
        display: block;
        margin: 10px 0;
        width: 100%;
        height: 38px;
        padding: 0 10px;
        box-sizing: border-box;
        border-radius: 10px;
    }

    .login {
        background-color: #d84a27;
        width: 220px;
        border-radius: 10px;
        color: white;
        border: 0;
    }

    .question {
        margin: 20px 10px;
        color: #333;
        font-family: '楷体';
        font-weight: bold;
    }

    .word {
        position: relative;
        box-sizing: border-box;
        border: 8px #fff100 solid;
        width: 100%;
        height: 160px;
        background-color: white;
        margin: 10px 0;
        border-radius: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 100px;
    }

    .word .status {
        position: absolute;
        right: 0px;
        bottom: 0px;
        z-index: 999;
    }
</style>
